<template>
  <center id="mainDiv">
    <div id="center">
      <div id="offeringInput">
        <!-- v-model input에서 값을 받아올 이름 지정 --> 
        <input type="text" v-model="offering.mname" placeholder="이름" class="input">
        <input type="text" v-model="offering.offerteg" placeholder="헌금종류" class="input">
        <input type="text" v-model="offering.amount" placeholder="금액" class="input" @keyup.enter="addOffering">
        
      </div>

      <br>

      <div id="quick">
        <button @click="editAmount(10000)">10000</button>  __ 
        <button @click="editAmount(30000)">30000</button>  __ 
        <button @click="editAmount(50000)">50000</button>
      </div>

      <hr>

      <table id="userTable">
        <tr><th>이름</th><th>헌금</th><th>금액</th><th>날짜</th></tr>

        <tr v-for="t in offeringList" v-bind:key="t.name">
          <td>{{t.mname}}</td><td>{{t.offertype}}</td><td>{{t.amount}}</td><td>{{t.dates}}</td><button>dlrpehjlsp?</button>
        </tr>
      </table>
    </div> 
  </center>
</template>

<script>
export default {
  
  data () {
    return {
      offering:{
        mname:'',
        offerteg:'',
        amount:'',
      },
      offeringList: [],
    }
  },
  methods:{
    addOffering(){
      this.$http.post('/api/addOffering',{
        offering: this.offering
      }).then((res) => {
          console.log(res)
          this.getOfferingList();
          this.inputClear();
        }).catch((err) => {
          console.log(err);
        }
      )
    },
    inputClear(){
      this.offering.mname = '';
      this.offering.offerteg = '';
      this.offering.amount = '';
    },
    getOfferingList(){
      this.$http.post('/api/getOfferingList')
        .then((res) => {
          this.offeringList = res.data
        })
    },
    editAmount(n){this.offering.amount = n;},
    editOfferType(n){this.offering.offerteg = n;}
  },
  created: function(){
    this.getOfferingList();
    this.getOfferTypes();
  },
}
</script>

